<template>
    <v-app>
        <v-main>
            <v-container>
                <v-icon>mdi-account-minus</v-icon>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
                <p class="text-center" :style="{'color': 'black'}">ID : {{ empId }}</p>
                <p class="text-center" :style="{'color': 'black'}"> : {{ empName }}</p>

            </v-container>

            <v-row justify="center">
                <v-col cols="auto">
                    <v-btn v-on:click="RETURN"></v-btn>
                </v-col>
                <v-col cols="auto">
                    <v-spacer></v-spacer> <!--  -->
                </v-col>
                <v-col cols="auto">
                    <v-btn v-on:click="CONFI"></v-btn>
                </v-col>
            </v-row>


        </v-main>
    </v-app>
</template>

<script>
    import axios from 'axios';
    export default {
        data() {
            return {
                errorMessage: '',
                messageVisible:false,
                empId: '', // ID
                empName: '', // 
                birth: '', // 
            };
        },
        created() {
            // 
            this.empId = this.$route.params.empId;
            this.empName = this.$route.params.empName;
            this.birth = this.$route.params.birth;
        }, 
        methods: {
            RETURN() {
                this.$router.push({ name: 'dele_Inp' });
            },
            CONFI() {
                axios
                    .request({
                        method: 'POST',
                        url: 'http://localhost:8080/delete_Func',
                        data: {
                            idOver: this.empId,
                            nameOver: this.empName,
                            birth: this.birth,
                        }
                    })
                    .then((response) => {
                        if (response.data === true) {
                            this.$router.push({ name: 'dele_Comp' });
                        }
                        else if (response.data === false) {
                            this.errorMessage = 'ID';
                            this.$router.push({ name: 'dele_Confi' });
                        }

                    })
            },
        }
    };
</script>