<template>
    <v-app>
        <v-main>
            <v-container>
                <v-icon>mdi-account-plus</v-icon>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
                <p class="text-center" :style="{'color': 'black'}">ID : {{ empId }}</p>
                <p class="text-center" :style="{'color': 'black'}"> : {{ empName }}</p>
                <p class="text-center" :style="{'color': 'black'}"> : {{ birth }}</p>
                <p class="text-center" :style="{'color': 'black'}"> : {{ age }}</p>
                <p class="text-center" :style="{'color': 'black'}"> : {{ gender }}</p>
                <p class="text-center" :style="{'color': 'black'}"> : {{ kihonkyu }}</p>
                <p class="text-center" :style="{'color': 'black'}"> : {{ koutuhi }}</p>


            </v-container>

            <v-row justify="center">
                <v-col cols="auto">
                    <v-btn v-on:click="RETURN"></v-btn>
                </v-col>
                <v-col cols="auto">
                    <v-spacer></v-spacer>
                </v-col>
                <v-col cols="auto">
                    <v-btn v-on:click="CONFI"></v-btn>
                </v-col>
            </v-row>


        </v-main>
    </v-app>
</template>

<script>
    import axios from 'axios';
    export default {
        data() {
            return {
                errorMessage: '',
                messageVisible:false,
                empId: '', 
                empName: '',
                birth: '',
                age: '',
                gender: '',
                kihonkyu: '',
                koutuhi:'',
            };
        },
        created() {

            this.empId = this.$route.params.empId;
            this.empName = this.$route.params.empName;
            this.birth = this.$route.params.birth;
            this.age = this.$route.params.age;
            this.gender = this.$route.params.gender;
            this.kihonkyu = this.$route.params.kihonkyu;
            this.koutuhi = this.$route.params.koutuhi;
        },
        methods: {
            RETURN() {
                this.$router.push({ name: 'ins_Inp' });
            },
            CONFI() {
                axios
                    .request({
                        method: 'POST',
                        url: 'http://localhost:8080/insert_Func',
                        data: {
                            idOver: this.empId,
                            nameOver: this.empName,
                            birth: this.birth,
                            age: this.age,
                            gender: this.gender,
                            kihonkyu: this.kihonkyu,
                            koutuhi: this.koutuhi,
                        }
                    })
                    .then((response) => {
                        if (response.data === true) {
                            this.$router.push({ name: 'ins_Comp' });
                        }
                        else if (response.data === false) {
                            this.errorMessage = 'ID';
                            this.$router.push({ name: 'ins_Confi' });
                        }

                    })
            },
        }
    };
</script>