<template>
    <v-app>
        <v-main>
            <v-container>

                <v-icon>mdi-account-arrow-right</v-icon>
                <p></p>
                <p></p>
                <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>

                <v-text-field label="ID" clearable v-model="inputEmpId" @input="checkEmpId" counter="4"
                              hint=" IDA001Z999
                              *ID"
                              :rules="[id_Max_Char]"></v-text-field>
                <p v-if="idError" class="error-message">ID</p>

                <v-text-field label="" clearable v-model="inputEmpName" @input="checkEmpName" counter="255"
                              hint="255"
                              :rules="[name_Max_Char]"></v-text-field>
                <p v-if="nameError" class="error-message"></p>

            </v-container>

            <v-row justify="center">
                <v-col cols="auto">
                    <v-btn v-on:click="LOGIN" :disabled="!inputEmpId || !inputEmpName"></v-btn>
                </v-col>
            </v-row>



        </v-main>
    </v-app>
</template>


<style type="text/css">
    p {
        font-family: 'Century', Times, serif;
        color:black;
    }
    .error-message {
        color: red;
    }
</style>

<script>
    import axios from 'axios';
    export default {
        data() {
            return {
                inputEmpId: '',
                idError: false,
                inputEmpName: '', 
                nameError: false,
                errorMessage: '',
            };
        },
        methods: {
            id_Max_Char(value) {
                if (value.length <= 4) {
                    return true;
                } else {
                    return 'ID4';
                }
            },
            name_Max_Char(value) {
                if (value.length <= 255) {
                    return true; 
                } else {
                    return '255'; 
                }
            },
            checkEmpId() {
                if (/[A-Z]{1}[0-9]{3}/.test(this.inputEmpId) && !/^(?!.*000).+$/.test(this.inputEmpId)) {
                    this.idError = true;
                } else {
                    this.idError = false;
                }
            },
            checkEmpName() {
                if (!/^[---]*$/.test(this.inputEmpName)) {
                    this.nameError = true;
                } else {
                    this.nameError = false;
                }
            },

            LOGIN() {
                axios
                    .request({
                        method: 'POST',
                        url: 'http://localhost:8080/Exist_Func',
                        data: {
                            idOver: this.inputEmpId,
                            nameOver: this.inputEmpName
                        }
                    })
                    .then((response) => {
                        if (response.data === 1) {
                            this.loggeIn = true;
                            this.$emit('loggeIn', this.loggeIn, this.inputEmpName);
                            this.$router.push({ name: 'sel', params: { loggIn: this.loggIn } });
                        }
                        else if (response.data === 0) {
                            this.errorMessage = 'ID';
                            this.$router.push({ name: 'user_Login' });
                        }

                    })
            },
            RETURN() {
                this.$router.push({ name: 'sel' });
            },
        }
    }

</script>